<template>
	<view class="content"></view>
</template>

<script>
	import { mapState, mapMutations } from 'vuex'
	import {homeApi} from '@/common/request/api.js'
	import {THEME_COLOR_TYPE,THEME_COLOR_VALUE} from '@/common/constant/themeColor.js'
	import {APP_singleLogin} from '@/common/request/uniqueApi/AZ.js'
	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapState(["singleLogin"])
		},
		onLoad(){
			// // #ifdef MP-WEIXIN
			// 	this.loadWX()//如果为微信登录
			// // #endif  
			// // #ifdef APP-PLUS || H5
			// 	if(this.singleLogin) this.singleLoginO()
			// 	else this.loadExecution()
			// // #endif
			if(this.singleLogin) this.singleLoginO()
			else this.loadExecution()
			this.getThemeStyle()
		},
		methods: {
			...mapMutations(["SET_LOGIN_STATE","UPDATE_THEME_COLOR"]),
			loadWX:function (){//微信登录
				const _t = this
				wx.getSetting({//判断之前是否已经授权
					success (res) {
						const authSettings = res.authSetting
						console.log(authSettings["scope.userInfo"])
						if(!authSettings["scope.userInfo"]){//如果没有授权进入授权页面
							uni.reLaunch({
							    url: '/pages/index/guide'
							});
						}else{
							//已经授权直接获取用户信息
							uni.login({
								provider: 'weixin',
								success: function(loginRes) {
									let code = loginRes.code;
									uni.getUserInfo({
										provider: 'weixin',
										success: function(infoRes) {
											let nickName = infoRes.userInfo.nickName; //昵称
											let avatarUrl = infoRes.userInfo.avatarUrl; //头像
											console.log(infoRes)
											homeApi.mpWXRegisterLogin(_t, {
												code:code,
												name:nickName
											})
										},
										fail(res) {}
									});
								},
							})
							console.log("您已经授权成功") 
						}
					}
				})
			},
			loadExecution: function(){
				console.log(uni.getSystemInfoSync().platform)
				/**
				 * 获取本地存储中launchFlag的值
				 * 若存在，说明不是首次启动，直接进入首页；
				 * 若不存在，说明是首次启动，进入引导页；
				 */
				try {
				    const launchFlag = uni.getStorageSync('launchFlag');
				    if (launchFlag) {
				        if (launchFlag == true) {//如果已经启动，判断是否是否记住密码
							const loginMsg = uni.getStorageSync('loginMsg')
				            uni.reLaunch({
				                url: '/pages/login/login'
				            });
				        } else {
				            uni.reLaunch({
				                url: '/pages/index/guide'
				            });
				        }
				    } else {
				        uni.setStorage({
				            key: 'launchFlag',
				            data: true,
				            success: function() {
								console.log('存储launchFlag');
							}
				        });
				        uni.reLaunch({
				            url: '/pages/index/guide'
				        });
				    }
				} catch(e) { 
					// error 
					uni.setStorage({ 
						key: 'launchFlag', 
						data: true, 
						success: function () {
							console.log('error时存储launchFlag');
						} 
					}); 
					uni.reLaunch({ url: '/pages/index/guide' }); 
				}
			},
			getThemeStyle:  function(){
				this.$http.post('pcHomePageConfig/selectline')
				.then(res => {
					const themeColor = res.data.data.appThemeColor
					if(!!themeColor){
						let param = {
							color: THEME_COLOR_TYPE[themeColor],
							colorCode: THEME_COLOR_VALUE[themeColor],
						}
						this.UPDATE_THEME_COLOR(param) //设置地址
					}
				})
				.catch(err => {
					console.log(JSON.stringify(err))
				});
			},
			singleLoginO(){//单点登录
				APP_singleLogin({},this).then( result => {
					const oldUserDesignerId = uni.getStorageSync("userDesignerId") || ""//用户设计组
					if(oldUserDesignerId != result.userDesignerId){
						//如果用户设计组不一样时，清空版本信息
						uni.setStorageSync("bundleVersion",{})
						uni.setStorageSync("userDesignerId",result.userDesignerId) //设置新的用户设计组
						uni.setStorageSync("language",result.currentLanguage) //当前语言
					}
					this.SET_LOGIN_STATE(result)
					uni.reLaunch({
						url:'/pages/home/home'
					})
				}).catch(err => {
					if(err.type === 'noTurnUp'){
						uni.showModal({
							title:this.$i18nMsg("lang-form-modalTipsError"),
							content:err.msg,
							showCancel:false,
							confirmText:this.$i18nMsg("lang-form-modalConfirm")
						})
					}else{
						this.loadExecution()
					}
				})
			}
		}
	}
</script>
